<template>
  <transition>
    <slot />
  </transition>
</template>

<script>
export default {
  name: "TransitionScroll",
  props: {
    enterClass: {
      type: String,
      required: true
    },
    leaveClass: {
      type: String,
    },
    delay: {
      type: Number,
      default: 0
    },
    threshold: {
      type: Number,
      default: 0.5
    }
  },
  mounted() {
    this.$el.addEventListener("animationend", this.removeClasses)
    this.$el.classList.add("is-invisible")
    let obsever = new IntersectionObserver((entries) => {
      if(entries[0].intersectionRatio > 0) {
        setTimeout(this.addClasses, this.delay)
        obsever.unobserve(this.$el)
      }
    }, {
      threshold: this.threshold
    })
    obsever.observe(this.$el)
  },
  methods: {
    addClasses() {
      this.$el.classList.remove("is-invisible")
      this.$el.classList.add('animate__animated', `animate__${this.enterClass}`)
    },
    removeClasses() {
      this.$el.classList.remove('animate__animated', `animate__${this.enterClass}`)
    }
  }
}
</script>

