<template>
	<div class="menu my-6">
		<div class="columns">
			<div class="column is-8 is-offset-1">
				<transition-scroll enterClass="fadeInLeft">
					<div id="menu" class="glide">
						<div class="glide__track" data-glide-el="track">
							<div class="columns is-multiline glide__slides">
								<div
									class="column is-6 glide__slide"
									v-for="(category, index) in menu"
									:key="category.name"
									:ref="`slide${index}`"
								>
									<h2 class="category-name">{{category.name}}</h2>
									<div class="category-line mt-3"></div>
									<ul class="category-list mt-4">
										<li class="item" v-for="(item, index) in category.items" :key="`${category.name + index}`">
											<div class="item-name">{{item.name}}</div>
										</li>
									</ul>
								</div>
							</div>
						</div>
					</div>
				</transition-scroll>
			</div>
			<div class="column is-2">
				<div class="menu__headings">
					<h1 class="title">
						<span class="is-underline">Menu</span>
					</h1>
					<p class="subtitle mt-5">Thc n a dng, phong ph.</p>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import menu from "@/assets/menu.js"
import Glide from "@glidejs/glide"
import { define } from "@glidejs/glide/src/utils/object"
import TransitionScroll from '@/components/TransitionScroll'
export default {
	data() {
		return {
			menu: menu,
			gliderWidth: 0,
			gliderItemWidth: 0
		}
	},
	computed: {
		gliderPerView() {
			return this.$mq === "mobile" ? 1 : 2
		}
	},
	components: {
		TransitionScroll
	},
	mounted() {
		this.calculateGliderFullWidth()
		var Sizes = function(Glide, Components, Events) {
			const Sizes = {
				setupSlides() {},
				setupWrapper() {}
			}

			define(Sizes, "slideWidth", {
				get() {
					return Components.Html.slides[0].offsetWidth
				}
			})

			define(Sizes, "wrapperSize", {
				get() {
					return Sizes.slideWidth * 4
				}
			})

			Events.on("destroy", () => {
				Sizes.emove()
			})

			return Sizes
		}

		let glide = new Glide("#menu", {
			gap: 0,
			rewind: false
		})
		glide.on("run.before", move => {
			if (move.direction === ">") {
				console.log(this.gliderPerView)
				let translate =
					(glide.index + 1 + this.gliderPerView) * this.gliderItemWidth
				console.log(translate)
				if (translate > this.gliderWidth) {
					move.steps = glide.index
					return move.direction = "="
				}
				move.steps = -this.gliderPerView
			} else if (move.direction === "<") {
				move.steps = this.gliderPerView
			}
		})
		glide.mount({
			Sizes
		})
	},
	methods: {
		calculateGliderFullWidth() {
			let length = this.menu.length
			let el = this.$refs[`slide${length - 1}`][0]
			this.gliderItemWidth = el.offsetWidth
			this.gliderWidth = el.offsetWidth + el.offsetLeft
			console.log(this.gliderWidth)
		}
	},
	watch: {
		$mq: {
			handler: "calculateGliderFullWidth"
		}
	}
}
</script>

<style scoped lang="scss">
.menu {
	&__headings {
		.title {
			font-size: 3.75em;
			font-weight: bold;
		}
		.subtitle {
			font-size: 1.5em;
		}
	}
}
#menu {
	padding: 3em;
	background-color: var(--background-primary-color);
	border-radius: 20px;
	overflow: hidden;
	& > .glide__track > .columns {
		flex-direction: column;
		max-height: 60vh;
	}
	.glide__slides {
		width: auto;
		overflow: visible;
	}
	.glide__track {
		overflow: visible;
	}
	.category-name {
		font-size: 1.5em;
		font-weight: bold;
	}
	.category-line {
		width: 50%;
		height: 4px;
		background-color: var(--primary-color);
	}
	.category-list {
		.item {
			display: flex;
			justify-content: space-between;
			font-size: 1.25em;
			margin-top: 1em;
		}
	}
}
</style>