<template>
	<div class="gallery" :class="$mq">
		<transition-scroll enterClass="fadeIn">
			<div class="columns">
				<div class="column is-10 is-offset-1">
					<div id="gallery_glider">
						<div class="glide__track" data-glide-el="track">
							<div class="glide__slides">
								<div
									v-for="(img, index) in galleryImages"
									:key="`${index}${img}`"
									class="gallery__photo glide__slide"
								>
									<mq-layout mq="widescreen+">
										<div class="photo__content">
											<h2 class="title">
												<span class="is-underline">{{index + 1 | getIndex}}</span>
											</h2>
											<p class="subtitle mt-4">{{img.content}}</p>
										</div>
									</mq-layout>
									<figure class="image is-16by9" :path="img.fileName">
										<img :src="getImageUrl(img.fileName)" />
									</figure>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</transition-scroll>
	</div>
</template>

<script>
import Glide from "@glidejs/glide"
import TransitionScroll from "@/components/TransitionScroll"
export default {
	data() {
		return {
			galleryImages: [
				{
					content: "Khng gian sn trc",
					fileName: "gallery_1"
				},
				{
					content: "Khng gian sn trc",
					fileName: "gallery_1"
        },
        {
          content: "Khong gian phia sau",
          fileName : "gallery_1"
        }
			]
		}
	},
	components: {
		TransitionScroll
	},
	mounted() {
		new Glide("#gallery_glider", {
			perView: 1,
      gap: 120,
      autoplay: 10000,
      hoverpause: false,
			animationDuration: 800,
			animationTimingFunc: 'ease-in-out',
		}).mount()
	},
	methods: {
		getImageUrl(img) {
			return require(`@/assets/${img}.png`)
		}
	},
	filters: {
		getIndex(val) {
			if (val < 10) return `0${val}`
			return val
		}
	}
}
</script>

<style scoped lang="scss">
.gallery {
	position: relative;
	z-index: 0;
	background-color: var(--background-primary-color);
	padding-top: 7.5em;
	&::after {
		content: "";
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 10vh;
		z-index: -1;
		background-color: var(--white-color);
	}
	&__photo {
		.photo__content {
			position: absolute;
			top: 0;
			left: 0;
			width: calc(33% - 40px);
			h2.title {
				font-size: 5em;
        font-weight: bold;
				&:first-letter {
					letter-spacing: 0.5em;
				}
			}
			p.subtitle {
				font-size: 2.25em;
				letter-spacing: 0.25em;
			}
		}
		&:nth-child(odd) {
			img {
				mask-image: url("../assets/gallery_mask.png");
				mask-size: 100%;
			}
		}
		&:nth-child(even) {
			img {
				mask-image: url("../assets/gallery_mask_flipped.png");
				mask-size: 100%;
			}
			.photo__content {
				position: absolute;
				top: 0;
				right: 0;
        left: auto;
				width: calc(33% - 40px);
        text-align: right;
			}
		}
	}
}

.glide__track,
.glide__slides {
	overflow: visible;
}
.glide__slide {
	opacity: 0.25;
	transition: opacity 0.3s ease;
	position: relative;
	&--active {
		opacity: 1;
	}
}
</style>